import React, { useRef } from 'react'
// import videoMainBg from '../../assets/images/home/videoMainBg.mp4'
// import videoMainBg from '../../assets/images/home/videoBg.mp4'
import videoMainBg from '../../assets/images/home/videoBgMain.webm'
import { ReactComponent as ArrowDown } from '../../assets/images/home/arrowDown.svg'
import { ReactComponent as GradientOverlay } from '../../assets/images/home/gradient_overlay.svg'
import { motion} from "framer-motion"
import { variants, variantsScale } from '../../const/animation'

const Main = ({}) => {
  // const cardVariants: Variants = {
  //   offscreen: {
  //     y: 500
  //   },
  //   onscreen: {
  //     y: 250,
  //     rotate: -10,
  //     transition: {
  //       type: "spring",
  //       bounce: 0.4,
  //       duration: 0.8
  //     }
  //   }
  // };

  return (
    <div className="container">
      {/*<div className="relative">*/}
        <video
          autoPlay
          loop
          muted
          className="video-bg"
        >
          <source src={ videoMainBg } type="video/mp4" />
        </video>
        {/*<GradientOverlay className='overlayBgMain' />*/}
      {/*</div>*/}
      <div className="wrapper main">
        <a href="#whyChooseUs" className="circle next-block"><ArrowDown /></a>

        <div className="info-text">
          <div>
            {/*<motion.h1 className="title leftOut" variants={variantsScale} initial='active' animate="inactive" custom={4} >UX/UI</motion.h1>*/}
            <h1 className="title leftOut fadeText">UX/UI</h1>
            {/*<motion.h1 className="title" variants={variantsScale} initial='active' animate="inactive" custom={6}>*/}
            {/*  Design Agency*/}
            {/*</motion.h1>*/}
            <h1 className="title fadeText" >
              Design Agency
            </h1>
          </div>
          {/*<motion.div className="description" variants={variantsScale} initial='active' animate="inactive" custom={8}>*/}
          {/*  With 18 years in user experience and brand identity we help forward-thinking companies create digital products that are intuitive,*/}
          {/*  engaging, and deliver results.*/}
          {/*</motion.div>*/}
          <div className="description fadeText">
            With 18 years in user experience and brand identity we help forward-thinking companies create digital products that are intuitive,
            engaging, and deliver results.
          </div>
        </div>
      </div>
    </div>
  )
}

export default Main