import SideNavigation from '../SideNavigation'
import Button from '../Button'
import topSemiCircle from '../../assets/images/home/top-half-icon.png'
import { useRef } from 'react'
import { useInView } from 'framer-motion'
import { motion } from 'framer-motion'
import { variants } from '../../const/animation'

const WhyUs = ({ isInView }) => {

  return (
    <div className="container light-300 vh-70" id="whyChooseUs">
      <img src={ topSemiCircle } alt="" className="bg-element" />
      <div className="wrapper">
        <div className="info-container">
          <div className="info-text">
            <motion.div
              className="title" variants={variants} initial='active' whileInView="inactive" custom={4}
            >
              Why us
            </motion.div>
            <motion.div className="description" variants={variants} initial='active' whileInView="inactive" custom={6}>
              <p>
                We define, design, and develop transformative experiences across all the brands touchpoints.
              </p>
              <br/>
              <p>
                With 18 years in user experience and brand identity we help forward-thinking companies create digital products that are
                intuitive, engaging, and deliver results.
              </p>
            </motion.div>
          </div>
          <Button title="Our Showcases" variants={variants} initial='active' whileInView="inactive" custom={6} />
        </div>
      </div>
    </div>
  )
}

export default WhyUs