import { useState, useEffect } from 'react'

const Counter = ({ val, time }) => {
  const [currVal, setCurrVal] = useState(0)
  useEffect(() => {
    currVal !== val && setTimeout(setCurrVal, time, currVal + 1)
  }, [currVal])
  return (
    <>
      <div className="counter">{ currVal }</div>
    </>
  )
}

export default Counter

