import React, { useEffect } from 'react'
import logo from '../assets/images/home/logo.png'
import { ReactComponent as LogoIcon } from '../assets/images/home/logoIcon.svg'
import { ReactComponent as Logo } from '../assets/images/home/logo.svg'
import { ReactComponent as MenuIcon } from '../assets/images/home/ico_menu.svg'
// import menuIcon from '../assets/images/home/ico_menu.png'
import logotype from '../assets/images/home/logotype.png'
import close from '../assets/images/home/close.png'
import { useState } from 'react'
import arrowRight from '../assets/images/home/ico_arrow.png'
import useScrollPosition from './useScrollPosition'
import arrowRightLight from '../assets/images/home/ico_arrow_light.png'
// import SidebarMenu from './SidebarMenu'

const Header = ({setActiveMenu}) => {

  const MobileMenu = () => {
    setActiveMenu(true)
  }
  const scrollPosition = useScrollPosition();
  // useEffect(() => {
  //   console.log('scrollPosition', scrollPosition)
  // }, [scrollPosition])
  let LogoIconc
  if (scrollPosition > 0) {
    LogoIconc = <LogoIcon className="fixed"/>
  } else {
    LogoIconc = <Logo />
  }

  return (
    <header className="header">
      <a href="/" className="logo">
        { LogoIconc }
        {/*<img src={ logo } alt="logo" />*/ }
      </a>
      <div className="header-info">
        <a href="/contact" className={ scrollPosition>0 ?'contactUs hideScroll' : 'contactUs' }>Contact Us</a>
        <MenuIcon className={ scrollPosition>0 ?'burger fixed' : 'burger' } onClick={ MobileMenu }/>
        {/*<img src={ menuIcon } alt="burger icon" className={ scrollPosition>0 ?'burger fixed' : 'burger' } onClick={ MobileMenu } />*/}
      </div>
    </header>
  )
}

export default Header